@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  color: white;
  margin: 0;
  padding: 0;
}

body {
  align-items: center;
  background-image: linear-gradient(#1a357c 9%, #99479b);
  background-size: cover;
  display: flex;
  height: 100vh;
  justify-content: center;
}

.App {
  text-align: center;
}

.btn {
  background-color: #1a357c;
  border: 1px solid transparent;
  border-radius: 5rem;
  box-shadow: 0.25rem 0.25rem 0.35rem rgba(0, 0, 0, 0.2);
  color: #99479b;
  cursor: pointer;
  font-weight: 700;
  padding: 0.75rem 1.5rem;
  transition: 0.25s;
}

.btn:hover {
  background: transparent;
  border: 1px solid white;
  box-shadow: 0.25rem 0.25rem 0.35rem rgba(0, 0, 0, 0.1);
  color: white;
}

.col {
  flex-basis: 48%;
  padding: 2rem;
}

.col input {
  background-color: rgba(255, 255, 255, 0.25);
  border: none;
  border-radius: 5rem;
  color: rgba(0, 0, 0, 0.75);
  font-weight: 700;
  outline: none;
  padding: 1rem;
  box-shadow: 0.25rem 0.25rem 0.35rem rgba(0, 0, 0, 0.2);
  text-align: center;
  transition: 0.25s;
}

.col input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.75);
  font-weight: 400;
}

.col input:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.75);
  font-weight: 400;
}

.col input::placeholder {
  color: rgba(255, 255, 255, 0.75);
  font-weight: 400;
}

.col input:active,
input:focus {
  background: rgba(255, 255, 255, 0.75);
  box-shadow: 0.5rem 0.5rem 0.25rem rgba(0, 0, 0, 0.1);
}

.col input:active::-webkit-input-placeholder, input:focus::-webkit-input-placeholder {
  color: grey;
  -webkit-transition: 0.25s;
  transition: 0.25s;
}

.col input:active:-ms-input-placeholder, input:focus:-ms-input-placeholder {
  color: grey;
  -ms-transition: 0.25s;
  transition: 0.25s;
}

.col input:active::placeholder,
input:focus::placeholder {
  color: grey;
  transition: 0.25s;
}

.row {
  display: flex;
  justify-content: space-between;
}

* {
  font-family: "Poppins", sans-serif;
}

.container {
  margin: 0;
  padding: 0;
}

.description {
  font-size: 0.9rem;
  font-weight: 200;
}

.location {
  font-weight: 500;
}

.minmaxTemp span {
  font-weight: 400;
  padding: 0 1rem;
}

.weather-icon {
  margin-bottom: 0.5rem;
  margin-top: 1rem;
}

.weather-icon img {
  background: rgba(255, 255, 255, 0.5);
  border-radius: 1rem;
  box-shadow: 0.25rem 0.25rem 0.35rem rgba(0, 0, 0, 0.1);
  height: 100%;
  width: auto;
}

